$color-white: #FFFFFF;
$color-black: #303030;
$color-bondi: #308090;
$color-gray-light: #F0F0F0;

$color-yellow: #ffec02;
$color-grey: #666666;
$color-violet: #8f1a81;

$options-bar-height: 80px;
$game-canvas-width: 1920px;
$game-canvas-height: 1080px;

@font-face {
  font-family: 'Exo 2';
  font-style: normal;
  font-weight: 300;
  src: local('Exo 2 Light'),
  url('../fonts/Exo2-VariableFont_wght.ttf') format('truetype');
}

@font-face {
  font-family: 'Exo 2';
  font-style: normal;
  font-weight: 400;
  src: local('Exo 2 Light'),
  url('../fonts/Exo2-VariableFont_wght.ttf') format('truetype');
}

body {
  position: absolute;
  top: 0;
  left: 0;
  width: $game-canvas-width;
  height: $game-canvas-height;
  font-family: 'Exo 2';
  font-weight: 100;
  background-color: black;
  padding: 0px;
  margin: 0px;
}

#central_info {
  position: absolute;
  width: 960px;
  left: 480px;
  top: 300px;
}

.report_text {
  font-family: 'Exo 2';
}

p.report_paragraph {
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
}

#report {
  z-index: 100;
  position: relative;
  margin: 0 auto;
  top: 0;
  width: 830px;
  padding: 20px;
  background-color: #FFF280;
  border-radius: 20px;
  opacity: 0;
  font-size: 22px;
}

.report_down {
  animation-duration: 1s;
  animation-name: reportDown;
  animation-fill-mode: forwards;
}

.report_up {
  animation-duration: 1s;
  animation-name: reportUp;
  animation-fill-mode: forwards;
}

@keyframes reportDown {
  from {
    top: 30px;
  }
  to {
    top: 300px;
  }
}

@keyframes reportUp {
  from {
    top: 300px;
  }
  to {
    top: 30px;
  }
}

.top_menu {
  top: 0;
}

.bottom_menu {
  top: 220px;
}

#menu {
  position: absolute;
  width: 480px;
  height: 280px;
  left: 240px;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 15px;
  z-index: 1000;
}

#menu_cursor_area {
  position: absolute;
  left: 20px;
  top: 50px;
  width: 60px;
  height: 430px;
  padding: 0;
  margin: 0;
}

#menu_options_area {
  position: absolute;
  top: 50px;
  left: 60px;
  width: 420px;
  height: 430px;
  padding: 0;
  margin: 0;
  line-height: 15px;
}

.button {
  height: 60px;
  margin: 0 0 15px 0;
  padding: 0px 10px 0 10px;
  border: 5px solid $color-yellow;
  border-radius: 15px;
  color: black;
  background-color: white;

  display: table-cell;
  vertical-align: middle;

  line-height: 80%;
  text-align: left;
  word-wrap: break-word;
  font-family: 'Exo 2';
  font-size: 20px;
}

.button:focus {
  outline: none;
}

.button:active {
  background-color: mix($color-yellow, white, 25%);
}

.menu_option {
  @extend .button;
  width: 380px;
}

.fade_in {
  animation: fade_in;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.fade_out {
  animation: fade_out;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

@keyframes fade_in {
  from {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

@keyframes fade_out {
  from {
    opacity: 1
  }
  to {
    opacity: 0
  }
}

.selected {
  background-color: $color-yellow;
}

#menu_cursor {
  width: 0;
  height: 0;
  margin: 15px 0 0 0;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  border-left: 30px solid $color-grey;
}

.info_element {
  position: absolute;
  width: 176px;
  height: 176px;
  margin: 2px;
  float: left;
  color: $color-white;
  z-index: 100;
  border-radius: 2px;
  opacity: 0;
  padding: 10px;
  font-size: 18px;

  .name {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

#menu_title {
  font-size: 24px;
  text-align: center;
  margin: 10px 10px 2px 10px;
}

h2 {
  font-size: 26px;
  text-align: center;
  margin: 10px 0 15px 0;
}

h3 {
  font-size: 24px;
  //  font-weight: bold;
  margin: 0 0 2px 0;
}

.info_description {
  color: $color-black;
}

#game {
  width: $game-canvas-width;
  height: $game-canvas-height;
  padding: 0px;
  margin: 0px;

  canvas {
    width: 100%;
    height: 100%;
    padding: 0px;
    margin: 0px;
    display: block;
  }
}

button {
  margin: 5px;
  border-width: 1px;
  background-color: white;
  color: #303030;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 5px;
  padding: 4px 8px 4px 8px;
}

$advance-button-width: 240px;
$advance-button-left: ($game-canvas-width - $advance-button-width)/2;

#advanceButton {
  z-index: 1001;
  position: absolute;
  display: none;
  left: $advance-button-left;
  width: $advance-button-width;
  bottom: 20px;
  margin-bottom: 0;
}

#languageButton {
  z-index: 1001;
  position: absolute;
  bottom: 20px;
  right: 20px;
  margin-bottom: 0;
}

#languageButton span.icon {
  position: relative;
  display: inline-block;
  height: 0px;
  line-height: 0px;
  vertical-align: middle;
  margin-left: 1ex;
}

$language-icon-height: 2.5em;
#languageButton * img.icon {
  position: relative;
  vertical-align: middle;
  height: $language-icon-height;
  top: -1.1*($language-icon-height/2);
}
